<template>
   <div class="warning-add">
       <p class="warning-add-title"></p>
       <el-form ref="form" :model="form" :rules="rules" label-width="100px">
            <el-form-item label="" prop="title">
                <el-input v-model="form.title" clearable></el-input>
            </el-form-item>
            <el-form-item label="" prop="type">
                <el-select v-model="form.type" placeholder="" style="width:100%">
                    <el-option
                    v-for="item in warningType"
                    :key="item.typeId"
                    :label="item.name"
                    :value="item.typeId">
                    </el-option>
                    
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="sendType">
                <el-col :span="11">
                    <el-form-item prop="sendType">
                        <el-select v-model="form.sendType" placeholder="" style="width:100%;">
                            <el-option value="1" label=""> </el-option>
                            <el-option value="2" label=""> </el-option>
                            <el-option value="3" label=""> </el-option>
                            <el-option value="4" label="+"> </el-option>
                            <el-option value="5" label="+"> </el-option>
                            <el-option value="6" label="+"> </el-option>
                            <el-option value="7" label="++"> </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="1">
                    <p style="display:inline-block;width:10px;">
                    </p>
                </el-col>
                <el-col :span="12">
                    <el-form-item prop="ContactId">
                        <el-select v-model="form.ContactId" multiple=""  placeholder="" style="width:100%;">
                            <el-option 
                            v-for="item in contact"
                            :key="item.id"
                            :value="item.id" 
                            :label="item.name"
                            > </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-form-item>
            <el-form-item label="">
                <el-col :span="8">
                    <el-form-item prop="cycle">
                        <el-input v-model="form.cycle" clearable placeholder="" type="number"><template slot="append"></template></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="1">
                    <p style="display:inline-block;width:10px;">
                    </p>
                </el-col>
                <el-col :span="7">
                    <el-form-item prop="continueCycle">
                        <el-input v-model="form.continueCycle" clearable placeholder="" type="number"><template slot="append"></template></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="1">
                    <p style="display:inline-block;width:10px;">
                    </p>
                </el-col>
                <el-col :span="7">
                    <el-form-item prop="silenceCycle">
                        <el-input v-model="form.silenceCycle" clearable placeholder="" type="number"><template slot="append"></template></el-input>
                    </el-form-item>
                </el-col>
            </el-form-item>
             <el-form-item label="" prop="countType">
                <el-col :span="8">
                    <el-form-item prop="countType">
                        <el-select v-model="form.countType" placeholder="">
                            <el-option
                            label=""
                            value="1">
                            </el-option>
                            <el-option
                            label=""
                            value="2">
                            </el-option>
                            <el-option
                            label=""
                            value="3">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="1">
                    <p style="display:inline-block;width:10px;">
                    </p>
                </el-col>
                <el-col :span="7">
                    <el-form-item prop="operator">
                        <el-select v-model="form.operator" placeholder="">
                            <el-option
                            label=""
                            value="1">
                            </el-option>
                            <el-option
                            label=""
                            value="2">
                            </el-option>
                            <el-option
                            label=""
                            value="3">
                            </el-option>
                            <el-option
                            label=""
                            value="4">
                            </el-option>
                            <el-option
                            label=""
                            value="5">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="1">
                    <p style="display:inline-block;width:10px;">
                    </p>
                </el-col>
                <el-col :span="7">
                    <el-form-item prop="value">
                        <el-input v-model="form.value" clearable placeholder="" type="number"></el-input>
                    </el-form-item>
                </el-col>
            </el-form-item>
            <el-form-item label="" prop="start">
                <el-col :span="11">
                    <el-form-item prop="start">
                        <el-time-picker
                            v-model="form.start"
                            placeholder=""
                            style="width:100%"
                            value-format="HH:mm:ss"
                            >
                        </el-time-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="2">
                    <p style="text-align:center;">
                        
                    </p>
                </el-col>
                <el-col :span="11">
                    <el-form-item prop="end">
                        <el-time-picker
                            v-model="form.end"
                            placeholder=""
                            style="width:100%"
                             :picker-options="{
                                minTime: form.start
                            }"
                            value-format="HH:mm:ss"
                            >
                        </el-time-picker>
                    </el-form-item>
                </el-col>
            </el-form-item>
             <el-form-item label="" prop="content">
                <el-input v-model="form.content" clearable></el-input>
            </el-form-item>
             <el-form-item label="" prop="status">
                 <el-switch
                    v-model="form.status"
                    active-color="#13ce66"
                    inactive-color="#ff4949"
                    :active-value='1'
                    :inactive-value='2'
                    >
                </el-switch>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('form')"></el-button>
                <el-button @click="cancel"></el-button>
            </el-form-item>
        </el-form>
   </div>
</template>
<script>
    export default{
        data(){
            return{
                form:{
                    title:'', //
                    type:'', //
                    sendType:'', //
                    cycle:'', //
                    continueCycle:'',//
                    countType:'',//
                    operator:'',//
                    silenceCycle:'',//
                    value:'',//
                    start:'',//
                    end:'',//
                    ContactId:'',//id
                    content:'',//
                    status:'1', //
                    warningType:null
                    
                },
                contact:null, //
                 rules: {
                    title: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    type: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    sendType: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    cycle: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    continueCycle: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    countType: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    operator: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    silenceCycle: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    value: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    start: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    end: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    ContactId: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    content: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    status: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                }
            }
        },
        components:{
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                if (valid) {
                     var params = {...this.form};
                     params.ContactId = params.ContactId.join(',');
                    capis.getNoticeSave(params,{})
                    .then(data=>{
                        this.tableLoading = false;
                        if(data.status == 'success'){
                            //
                             this.$emit('freshData',{
                                type:'add',
                            })
                        }else{
                            this.$notify({
                                title: '',
                                message: data.des,
                                type: 'warning'
                            });
                        }
                    
                    })
                    .catch(err=>{
                        this.tableLoading = false;
                        this.$notify({
                            title: '',
                            message: '',
                            type: 'warning'
                        });
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
                });
            },
            getContactListEnable(){
                //
                var params = {};
                capis.getContactListEnable(params,{})
                .then(data=>{
                    this.tableLoading = false;
                    if(data.status == 'success'){
                        //
                        this.contact = data.res.data;
                    }else{
                        this.$notify({
                            title: '',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'warning'
                    });
                })
            },
            cancel(){
               this.$emit('freshData',{
                    type:'cancel',
                })
            },
            getWarnType(){
                this.warningType = ctool.getWarningType();
            }
        },
        created(){
            this.getWarnType();
            this.getContactListEnable();
        }
    }
</script>
<style scoped lang="scss">
   .warning-add{
       padding: 0 80px;
       .warning-add-title{
           font-size: 28px;
           font-weight: 600;
           height: 60px;
           line-height: 60px;
       }
   }
</style>


