 * @Descripttion: 
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-11 22:42:04
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-26 10:12:48
-->
<template>
    <div class="devices">
        <div class="message-top" ref="message-top">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" @keydown.enter.native="devicesList('search')">
                <el-form-item label="/">
                    <el-input v-model="searchForm.keywords" placeholder="" clearable></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="searchForm.status" placeholder="" clearable>
                        <el-option label="" value="1"></el-option>
                        <el-option label="" value="2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" icon="el-icon-search" @click="devicesList('search')" :loading="tableLoading"></el-button>
                    <el-button icon="el-icon-circle-plus-outline" type="success" @click="serviceShow = !serviceShow"></el-button>
                </el-form-item>
            </el-form>
            
        </div>
        <div class="message-table">
            <el-table
                ref="record-table"
                :data="list"
                style="width: 100%"
                stripe
                :height = "tableHeight"
                highlight-current-row
                v-loading="tableLoading"
            >
                <el-table-column
                    type="index"
                    width="50">
                </el-table-column>
                <el-table-column
                label=""
                 prop="name"
                sortable
                show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                label=""
                 prop="host"
                sortable
                show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                label=""
                sortable
                show-overflow-tooltip
                >
                <template slot-scope="scope">
                       <p v-if="scope.row.type == 1">centos</p>
                </template>
                </el-table-column>
                <el-table-column
                label=""
                show-overflow-tooltip
                >
                <template slot-scope="scope">
                    <el-link type="primary" @click="noticeInfo(scope);"></el-link>
                </template>
                </el-table-column>
                <el-table-column
                label=""
                 prop="updated_at"
                sortable
                fixed="right"
                show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                label=""
                >
                    <template slot-scope="scope">
                        <el-switch
                            v-model="scope.row.status"
                            active-color="#13ce66"
                            inactive-color="#ff4949"
                            :active-value='1'
                            :inactive-value='2'
                            @change="changeStatus(scope)"
                            >
                        </el-switch>
                    </template>
                </el-table-column>
                 <el-table-column
                label=""
                >
                    <template slot-scope="scope">
                       <div class="dedevice-run">
                           <p style="width:12px;height:12px;border-radius:50%;margin-right:5px;" :style="{backgroundColor:(scope.row.running?'#05f360':'red')}"></p>
                           <p v-if="scope.row.running"></p>
                           <p v-if="!scope.row.running"></p>
                       </div>
                    </template>
                </el-table-column>
                <el-table-column
                label=""
                fixed="right"
                width="200"
                >
                    <template slot-scope="scope">
                        <el-button type="primary" icon="el-icon-edit" size="mini" @click="editInfo(scope);"></el-button>
                        <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteDevies(scope);"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="message-pagenation" ref="message-pagenation">
            <el-pagination
                :page-sizes="[10, 20, 50, 100]"
                :page-size="pagenation.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pagenation.total"
                :current-page="pagenation.page"
                @size-change='sizeChange'
                @current-change='curreantChange'
                >
            </el-pagination>
        </div>
       <el-dialog width="50%" :visible.sync="serviceShow" :lock-scroll="true" :center="true" :show-close="false" custom-class="service_dialog" :destroy-on-close="true">
            <DevicesAdd v-on:freshData="freshData"></DevicesAdd>
        </el-dialog>
        <el-dialog width="50%" :visible.sync="serviceEditShow" :lock-scroll="true" :center="true" :show-close="false" custom-class="service_dialog" :destroy-on-close="true">
            <DevicesEdit v-on:freshData="freshData" v-if="serviceEditShow" :editId.sync='editId'></DevicesEdit>
        </el-dialog>
        <el-dialog width="50%" :visible.sync="noticeShow" :lock-scroll="true" :center="true" :show-close="false" custom-class="service_dialog" :destroy-on-close="true">
            <Notice v-on:freshData="freshData" v-if="noticeShow" :hostId.sync='hostId'></Notice>
        </el-dialog>
    </div>
</template>
<script>
    import DevicesAdd from './add/add.vue'
    import DevicesEdit from './add/edit.vue'
    import Notice from './add/notice.vue'
    export default{
        data(){
            return{
                list:[],//
                tableHeight:600, // 
                serviceShow:false,
                serviceEditShow:false,
                noticeShow:false, // 
                editId:'',
                hostId:'',
                pagenation:{
                    page:1,
                    pagesize:10,
                    total:0
                },
                tableLoading:false, //
                editId:'', //id
                searchForm:{
                    keywords:'',
                    status:''
                }
            }
        },
        methods:{
            getHeight(){
                this.tableHeight = window.innerHeight-this.$refs['record-table'].$el.offsetTop-50;
            },
            deleteDevies(data)
            {
                // 
                 this.$confirm(''+data.row.name+'?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                    }).then(() => {
                        var params = {
                           id:data.row.id
                        }
                        capis.devicesDel(params,{})
                        .then(data=>{
                            this.tableLoading = false;
                            if(data.status == 'success'){
                                //
                                this.list.splice(data.$index,1);
                                this.$notify({
                                    type: 'success',
                                    message: '!'
                                });
                            }else{
                                this.$notify({
                                    title: '',
                                    message: data.des,
                                    type: 'warning'
                                });
                            }
                        
                        })
                        .catch(err=>{
                            this.tableLoading = false;
                            this.$notify({
                                title: '',
                                message: '',
                                type: 'warning'
                            });
                        })
                                
                    }).catch(() => {
                        
                });
            },
            devicesList(type){
                if(type=='search')
                {
                    //
                    this.pagenation.page=1;
                }
                //
                this.tableLoading = true; //
                var params = {
                    page:this.pagenation.page,
                    pagesize:this.pagenation.pagesize,
                }
                params = {...params,...this.searchForm};
                console.log(params,this.searchForm)
                capis.devicesList(params,{})
                .then(data=>{
                    this.tableLoading = false;
                    if(data.status == 'success'){
                        //
                        this.list = data.res.data;
                        //
                        this.pagenation.total=data.res.total;
                    }else{
                        this.$notify({
                            title: '',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'warning'
                    });
                })
            },
            freshData(data)
            {
                // 
                if(data && data.type == 'cancel')
                {
                    // ,
                    
                }
                else if(data && data.type == 'update')
                {
                    // 
                    this.devicesList();
                }
                else if(data && data.type == 'add')
                {
                    // 
                    //1
                    this.pagenation.page = 1;
                    this.devicesList();
                }
                this.serviceShow = false;
                this.serviceEditShow = false;
            },
            changeStatus(data)
            {
                 this.formLoading=true;
                    //
                     var params = {
                        act:'changeStatus',
                        id:data.row.id,
                        status:data.row.status
                     }
                    capis.devicesSave(params,{})
                    .then(data=>{
                        if(data.status == 'success'){
                            this.$notify({
                                title: '',
                                message: '',
                                type: 'success'
                            });
                        }else{
                            this.$notify({
                                title: '',
                                message: data.des,
                                type: 'warning'
                            });
                        }
                    
                    })
                    .catch(err=>{
                        this.$notify({
                            title: '',
                            message: '',
                            type: 'warning'
                        });
                    })
            },
            sizeChange(pagesize){
                //
                this.pagenation.pagesize = pagesize;
                this.devicesList();
            },
            curreantChange(page)
            {
                this.pagenation.page = page;
                this.devicesList();
            },
            editInfo(data)
            {
                this.editId = data.row.id;
                this.serviceEditShow = true ;
            },
            noticeInfo(data)
            {
                this.hostId = data.row.id;
                this.noticeShow = true ;
            },
            initWs(){
                //
                this.$LaravelEcho.instance.channel('HostStatus').listen('HostEvent', (e) => {
                    if(e.type=="status")
                    {
                        if(this.list.length>0)
                        {
                            this.list.forEach((value,index) => {
                                if(value.id==e.host_id)
                                {
                                    this.list[index].running = e.data.running;
                                }
                            });
                        }
                    }
                })
            }
        },
        created(){
            //
            this.devicesList();
            this.$nextTick(()=>{
                this.getHeight();
                this.initWs();
                window.onresize = () => {
                    this.getHeight();
                };
            });
        },
        components:{
            DevicesAdd
            ,DevicesEdit
            ,Notice
        },
        computed:{
            
        },
        beforeDestroy(){
            console.log(":HostStatus");
            this.$LaravelEcho.instance.leaveChannel('HostStatus');
        }
    }
</script>

<style scoped lang="scss">
    .devices{
        box-sizing: border-box;
        background-color: #fff;
        box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
        border-radius: 20px;
        padding: 40px 40px;
        height: 100%;
        .message-top{
            .message-title{
                font-size: 24px;
                font-weight: 900;
            }
            .message-type{
                box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
            }
        }
        .message-pagenation{
            text-align: center;
            padding: 10px 0;
        }
        .dedevice-run{
            display: flex;
            align-items: center;
        }
    }
</style>
<style>
.service_dialog{
       border-radius: 15px;
       box-shadow: 0 0 30px 0 rgba(94, 94, 198, 0.7);
       box-sizing: border-box;
   }
</style>



